.nav {
  width: max-content;
  height: 100%;
  display: flex;

  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 11px 0;
  }

  &__link {
    text-transform: uppercase;
    color: $header-link-color;
    text-decoration: none;
    cursor: pointer;
    position: relative;
    height: 100%;
    padding: 23px 0;

    &:hover {
      color: $header-link-active-color;
    }
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.is-active {
  color: $header-link-active-color;

  &::after {
    content: "";
    background-color: $header-link-active-color;
    height: 4px;
    width: 100%;
    border-radius: 8px;
    bottom: 0;
    left: 0;
    position: absolute;
  }
}
